.player {
	min-height: 20vh;
	padding-left: 10%;
	padding-right: 10%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}

.time-control {
	width: 60%;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	margin-top: 1rem;
	input {
		-webkit-appearance: none;
		width: 100%;
		background: transparent;
		cursor: pointer;
	}
	p {
		width: 5rem;
		padding: 0 1rem;
	}

	@media screen and (max-width: 768px) {
		width: 90%;
	}
}

.play-control {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1rem;
	width: 35%;
	svg {
		cursor: pointer;
	}

	@media screen and (max-width: 768px) {
		width: 60%;
	}
}

input[type="range"]:focus {
	outline: none;
}

input[type="range"]::-webkit-slider-thumb {
	-webkit-appearance: none;
}

input[type="range"]::-moz-range-thumb {
	-webkit-appearance: none;
	background: transparent;
	border: none;
}

.track {
	background: lightblue;
	width: 100%;
	height: 0.5rem;
	position: relative;
	border-radius: 1rem;
	overflow: hidden;
}
.animate-track {
	background: rgb(204, 204, 204);
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	transform: translateX(0%);
	pointer-events: none;
}
